.browse-card{
    border: 1px solid gray;
    border-radius: 20px;
    margin: 30px 0px 20px 0px;
    text-align: start;
    padding: 20px;
}
.browse-card p{
    color: gray;
}
.browse-card p:last-of-type{
    font-size: 15px;
}
.browse-card p:nth-child(1){
    width: 50px;
    height: 50px;
    text-align: center;
    background-color: white;
    padding-top: 3px;
    border-radius: 50%;
    color: var(--color-primary);
    font-size: 25px;
}
.browse-card h5{
    color: white;
}
