.slider-container{
    min-height: 0px;
    min-width: 0px;
    padding: 20px;
    background-color: var(--color-darkest);
    border-radius: 20px;
    position: relative;
    margin-bottom: 20px;
}
.slider-container .carousel-header{
    color: var(--color-light);
    text-decoration: underline;
    font-weight: 700;
    font-size: 25px;
}
.slider-item .image-container{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow-y: hidden;
}
.slider-item .image-container img{
    text-align: center;
    margin: auto;
    border-radius: 10px;
    width: 90%;
    border-radius: 20px;
}
.slick-prev{
    left: 80%;
}
.slick-list{
    margin-top: 20px;
}
.slide-caption{
    color: var(--color-light);
    padding: 10px 20px;
}
.slide-caption h5{
    font-size: 17px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.slide-caption h6{
    font-size: 13px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.slide-caption h6 span:nth-child(1){
    color: gray;
}
.over-img{
    width: 80%;
    margin: auto;
    text-align: center;
    padding: 10px 20px;
    color: var(--color-primary);
    border-radius: 20px;
    background-color: var(--color-darkest);
     position: absolute;
     bottom: -50px;
    transition:0.5s all ease-in-out;
}
.over-img p{
    margin-bottom: 0px;
}
.image-container:hover> .over-img{
    bottom: 15px;
    transition: bottom 0.5s;
}
@media(max-width:600px){
    .slider-item .image-container img{
        width: 90%;
       height: 300px;
    }
}