.gaming-library-card{
    max-width: 100%;
    flex: 1 1 100%;
    color: white;
}
.gaming-library-card ul{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    border-bottom: 1px solid var(--color-primary);
    padding: calc(50px/2) 0;
}
.gaming-library-card ul li{
    margin-top: 10px;
}
.gaming-library-card ul li img{
    width: 80px;
    border-radius: calc(25px/2);
}
.gaming-library-card ul li h4{
    font-size: 1rem;
}
.gaming-library-card ul li span{
    color: gray;
    font-size: 14px;
}
.gaming-library-card .main-button{
    margin-top: 10px;
}
@media(max-width:991px){
    .gaming-library-card ul{
        flex-direction: row;
    }
}
@media(max-width:768px){
    .gaming-library-card ul{
        flex-direction: column;
        text-align: center;
    }
    .gaming-library-card ul li img{
        width: 50%;
        margin-bottom: 60px;
    }
}