.most-popular-item{
    flex:1 25%;
}
.card-wrapper{
    background-color: var(--color-dark);
    border-radius: calc(25px/2);
    padding: calc(50px/2);
    margin: calc(50px/5);
}
.most-popular-item-img{
    width: 100%;
    height: 120px;
    border-radius: calc(25px/2);
    margin-bottom: calc(50px/4);
}
.most-popular-item-content{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color:white;
}
.most-popular-item-content ul li{
    display:  flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-end;
    font-size: 13px;
}
.most-popular-item-title{
    font-size: 14px;
    line-height: 1.5rem;
}
@media(max-width:768px){
    .card-wrapper{
        padding: 0px;
        margin: 0px;
        margin-top: 20px;
        margin-bottom: 20px;
    }
}
.profilecard{
    background-color: var(--color-dark);
    padding: 30px 20px;
    border-radius: 20px;
    margin:0px 5px 16px 5px;
    width: 23%;
}
.prof-card-image{
    height: 140px;
    background-size: cover;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.prof-card-image p{
    background-color: var(--color-light);
    height: 50px;
    width: 50px;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.prof-card-caption{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: 15px;
}
.prof-card-caption p{
    color: var(--color-light);
    margin-bottom: 0px;
}
.prof-card-cap-icon{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.prof-card-cap-icon p{
    color: var(--color-primary);
    margin-bottom: 0px;
    font-size: 20px;
    margin-right: 7px;
}
.prof-card-cap-icon span{
    color: var(--color-light) !important;
    font-size: 14px !important;
    margin-top: 4px;
}
@media(max-width:991px){
    .profilecard{
        width: 80%;
        margin: auto;
        margin-bottom: 20px;
    }
    .most-popular-item{
        flex:1 50%;
    }
    
}
@media(max-width:768px){
    .profilecard{
        width: 90% !important;
        margin: auto;
        margin-bottom: 20px;
    }
    .most-popular-item{
        flex:1 60%;
    }
    .card-wrapper{
        padding: calc(50px/2);
        margin: calc(50px/5);
    }
}