.nav-item-down{
    width: 0%;
    height: 3px;
    background-color: var(--color-primary);
    transition: width 1s;
}

.nav-item:hover> .nav-item-down{
    width: 95%;
    transition: width 0.5s;
}
.dropdown-item{
    color: var(--color-primary);
    transition: 0.5s all ease-in-out;
}