:root{
    --color-dark:#27292a;
    --color-light:white;
    --color-primary:#ec6090;
    --color-darkest:#1f2122;
}
ul{
    list-style-type: none;
}
a{
    text-decoration: none;
}
.color-white{
    color: white !important;
}
::selection{
    background-color: var(--color-primary);
    color: var(--color-light);
}