.to-up-button {
    position: fixed;
    bottom: 20px;
    right: 20px;
    background-color: var(--color-primary);
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 15px;
    cursor: pointer;
    transition: opacity 0.3s;
    z-index: 2;
}

.to-up-button.visible {
    opacity: 1;
}

.to-up-button:not(.visible) {
    opacity: 0;
}