.right-side{
    background-color: var(--color-darkest);
    border-radius: 20px;
    padding: 20px 20px;
}
.right-side h3{
    color: white;
    text-decoration: underline;
    font-size: 30px;
    font-weight: 700;
    display: inline-block;
    margin-right: 3px;
}
.right-side .right-side-header{
    color: var(--color-primary);
    font-size: 25px;
    font-weight: 600;
}
.right-side-item{
    display: flex;
    flex-direction: row;
    margin-top: 20px;
}
.right-side-item img{
    width: 80px;
    height: 80px;
    border-radius: 10px;
}
.right-side-caption{
    margin-left: 10px;
    width: 75%
}
.right-side-caption h5{
    color: var(--color-light);
    font-size: 18px;
    margin: 0px;
}
.right-side-caption h6{
  color:gray ;
  font-size: 13px;
}
.right-side-icons{
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    margin-top: 15px;
}
.right-side-icons p{
    margin-bottom: 0px;
}
.right-side-icons p:nth-child(2){
    color: var(--color-primary);
}
.right-side-icons p:nth-child(1){
    color: yellow;
}
.right-side-icons p span{
    color:  white;
    font-size: 13px;
}
.right-side-icons button{
    background-color: var(--color-dark);
    padding-bottom:5px ;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-top: -10px;
    border: none;
}
.right-side-icons button a{
    color: var(--color-primary);
}
.right-side .right-side-button{
    background-color: transparent;
    border: none;
    width: 100%;
    text-align: center;
    margin-top: 20px;
}