.profile-image{
    width: 95%;
    border-radius: 20px;
}
.palyer-state{
    padding: 20px 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin-bottom: 20px;
}
.palyer-state .state{
    width: 75px;
    padding: 6px 12px;
    background-color: var(--color-primary);
    border-radius: 15px;
    color: white;
}
.palyer-state h4{
    color: white;
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 25px;
}
.profile-caption{
    color: gray;
}
.palyer-info{
    background-color: var(--color-dark);
    border-radius: 20px;
}
.palyer-info .player-info-details{
    list-style: none;
    padding: 5px 5px;
}
.palyer-info .player-info-details .player-info-item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 15px 15px;
}
.palyer-info .player-info-details .player-info-item p{
    margin-bottom: 0px;
}
.palyer-info .player-info-details .player-info-item p:nth-child(1){
  color: gray;
}
.palyer-info .player-info-details .player-info-item p:nth-child(2){
color: var(--color-primary);
}
.palyer-info .player-info-details hr{
    color: gray;
    margin: 6px auto;
    width: 92%;
}
.Clips{
    margin-top: 30px;
}
.edge hr{
    width: 100%;
    margin-top: 25px;
    color: gray;
}
.Clips h4{
    font-size: 36px;
    color: white;
    font-weight: 700;
    text-decoration: underline;
    display: inline-block;
}
.Clips span{
    color: var(--color-primary);
    font-size: 36px;
    font-weight: 600;
}
.profCards{
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}
.clips-button{
    text-align: center;
    margin: auto;
    margin-top: 50px;
}
@media(max-width:678px){
    .Clips h4{
        font-size: 20px !important;
    }
    .Clips span{
        font-size: 20px;
    }
}
