.stream-item{
    margin-bottom: 20px;
}
.image-side{
    padding: 10px;
    height: 200px;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
}
.image-side .upper-side{
  background-color: var(--color-primary);
  border-radius: 10px;
  text-align: center;
  padding: 2px 10px;
  color: white;
  transition: 0.5s all ease-in-out;
}
.image-side .upper-side p{
    margin-bottom: 0px;
}
.image-side .bottom-side{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    transition: 0.5s all ease-in-out;
}
.image-side .bottom-side p{
    margin-bottom: 0px;
    background-color: var(--color-primary);
    padding: 2px 10px;
    border-radius: 10px;
    color: white;
}
.hide-side{
    opacity: 0;
    transition: 0.5s all ease-in-out;
}
.image-side:hover> .hide-side{
    opacity: 1;
}
.caption-side{
    margin-top: 20px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.caption-side .caption-image img{
    border-radius: 50%;
}
.caption-side .caption-details{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 10px;
}
.caption-side .caption-details div{
    display: flex;
    flex-direction: row;
}
.caption-side .caption-details div h5{
    margin-left: 3px;
    color: white;
}
.caption-side .caption-details div span{
    color: var(--color-primary);
}
.caption-side .caption-details h3{
    color: grey;
    font-size: 17px;
}