.Hero-main{
 background: url('../../assests/images/banner-bg.jpg') no-repeat top right/cover;
 background-color: var(--color-dark);
 color: var(--color-light);
 border-radius: 25px;
 padding:50px;
}
.Hero-text{
    max-width: 500px;
}
.Hero-subtitle{
    font-size: 20px;
    color: var(--color-light);
    font-weight: 400;
    margin-bottom: 25px;
}
.Hero-title{
    font-size: 60px;
    text-transform: uppercase;
    margin-bottom: 25px;
    font-weight: 900;
}
.Hero-title em{
    font-style: normal;
    color: var(--color-primary);
}
@media(max-width:768px){
    .Hero-title{
        font-size: 20px;
    }
}