.left-image{
    width: 100%;
    border-radius: 20px;
    padding: 20px;
}
.left-image img{
    width: 100%;
    border-radius: 20px;
}
.right-image{
    padding: 20px;
    margin-left: 20px;
    margin-right: 20px;
    text-align: center;
    margin-top: 20px;
    border-radius: 20px;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-size: cover;
}
.right-image p{
   width: 50px;
   height: 50px;
   background-color: white;
   border-radius: 50%;
   padding-top: 6px;
   padding-left: 1px;
   margin-bottom: 0px;
}
.right-image p a{
    text-align: center;
    font-size: 20px;
    color: var(--color-primary);
}
.game-style{
    margin-top: 80px;
}
.game-style h2{
    text-align: center;
    color: white;
    font-size: 40px;
    font-weight: 700;
}
.game-style .game-caption{
    margin-top: 20px;
    padding: 20px;
    color: gray;
    font-size: 15px;
}
.game-details{
    margin-top: 80px;
    background-color: var(--color-darkest);
    padding:30px;
    border-radius: 20px;
}

.side-details .partation{
    border-radius: 20px;
    background-color: var(--color-dark);
    padding: 25px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}
.side-details .partation h6{
    margin-bottom: 0px;
    line-height: 50px;
    color: white;
    font-size: 16px;
}
.side-details .partation h6 p{
    margin-bottom: 0px;
}
.game-details-image{
    margin-top: 30px;
    padding: 20px;
}
.game-details-image img{
    border-radius: 20px;
    text-align: center;
    margin: auto;
    max-width: 100%;
}
.Details .row{
    justify-content: space-between;
    flex-wrap: wrap;
}
@media(max-width:500px){
    .side-details .partation {
        flex-direction: column;
    }
    .side-details .partation h6{
        line-height: 30px;
        margin-bottom: 20px;
    }
}